/* Default styles for the navigation bar */
.navbar {
    background-color: #f8f9fa;
}

.navbar-brand {
    font-size: 24px;
}

.nav-link {
    font-size: 16px;
    padding: 8px 16px;
}

/* Media query for screens up to 768px wide (typical mobile screens) */
@media (max-width: 768px) {
    .navbar-brand {
        font-size: 20px;
    }

    .nav-link {
        font-size: 18px;
        padding: 8px;
    }

    .navbar-toggler {
        background-color: #007bff;
    }

    .navbar-toggler-icon {
        background-color: #fff;
    }

    .navbar-collapse {
        background-color: #f8f9fa;
        position: absolute;
        top: 56px;
        left: 0;
        right: 0;
        text-align: center;
    }

    .navbar-nav {
        display: flex;
        flex-direction: column;
        padding: 8px;
    }

    .nav-item {
        padding: 8px 0;
    }
}