/* Default styles for larger screens */
.header {
    background-color: #f0f0f0;
    padding: 20px;
    text-align: center;
}

.hero-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.hero-left {
    flex: 1px;
   
}

.hero-title {
    text-align: left;
}

.hero-title h1 {
    font-size: 28px;
    margin: 0;
}

.hero-description {
    margin-top: 5px;
}

/* Common flex styles */
.flexCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

.flexColStart {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

/* Media query for mobile screens */
@media (max-width: 768px) {
    .hero-container {
        flex-direction: column;
        align-items: center;
    }

    .hero-left {
        text-align: center;
    }

    .hero-title h1 {
        font-size: 24px;
    }

    .hero-description {
        text-align: center;
    }

    /* Add additional styles for mobile screens as needed */
}
